<script>
import { ref, reactive, onMounted } from "vue";
import { CONTRACT_ABI, CONTRACT_ADDRESS } from "@/assets/artKonnection.js";
import Caver from "caver-js";

export default {
  setup() {
    let navbarToggle = ref(true);
    let dropdown = ref(false);
    let darkMode = ref("light");
    const klaytnscopeURL = ref(`${process.env.VUE_APP_KLAYTN_SCOPE_URL}`);
    const openseaURL = ref(`${process.env.VUE_APP_OPENSEA_URL}`);
    const toggleDark = () => {
      if (darkMode.value == "dark") {
        darkMode.value = "light";
        localStorage.setItem("artKonnectionDarkmode", "light");
      } else {
        darkMode.value = "dark";
        localStorage.setItem("artKonnectionDarkmode", "dark");
      }
    };
    let rout = reactive([
      {
        path: "/about",
        name: "About",
      },
      {
        path: "/tokens",
        name: "Tokens",
      },
      {
        path: "/collections",
        name: "Collections",
      },
      {
        path: "/inmintingproject",
        name: "Minting Project",
      },
      {
        path: "/roadmap",
        name: "Roadmap",
      },
    ]);

    let admin = ref(false);
    let caver = null;
    let contract = null;
    onMounted(async () => {
      darkMode.value = localStorage.getItem("artKonnectionDarkmode");
      if (window.klaytn === undefined) {
        alert(
          "    PC      ."
        );
        window.open(
          "https://art-konnection.gitbook.io/artkonnection/kaikas-guide/install-kaikas",
          "_blank"
        );
        return;
      }
      onLogin();
      window.klaytn.on("accountsChanged", () => {
        walletToShow.value =
          String(window.klaytn.selectedAddress).slice(0, 4) +
          "..." +
          String(window.klaytn.selectedAddress).slice(-4);
        localStorage.setItem("walletAddress", window.klaytn.selectedAddress);
        onLogin();
      });
    });

    let walletToShow = ref("Connect Wallet");
    const onLogin = async () => {
      await window.klaytn.enable();
      caver = new Caver(
        window.klaytn._kaikas.isEnabled()
          ? window.klaytn
          : process.env.VUE_APP_RPC_URL
      );
      contract = new caver.klay.Contract(CONTRACT_ABI, CONTRACT_ADDRESS);
      if (window.klaytn.selectedAddress !== undefined) {
        localStorage.setItem(
          "walletAddress",
          String(window.klaytn.selectedAddress)
        );
        walletToShow.value =
          String(window.klaytn.selectedAddress).slice(0, 4) +
          "..." +
          String(window.klaytn.selectedAddress).slice(-4);

        isOwner(window.klaytn.selectedAddress);
        artistCheck(window.klaytn.selectedAddress);
      }
    };

    let artistProjects = ref(false);
    const artistCheck = async (_address) => {
      await contract.methods
        .getArtistProjectIds(_address)
        .call()
        .then((res) => {
          if (res.length > 0) {
            artistProjects.value = true;
          } else {
            artistProjects.value = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    };
    const isOwner = async (_addr) => {
      await contract.methods
        .isOwner()
        .call({
          from: _addr,
        })
        .then((result) => {
          if (result) {
            admin.value = true;
          } else {
            admin.value = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    };

    return {
      rout,
      walletToShow,
      onLogin,
      admin,
      darkMode,
      toggleDark,
      artistProjects,
      CONTRACT_ADDRESS,
      klaytnscopeURL,
      openseaURL,
      navbarToggle,
      dropdown,
    };
  },
};
</script>

