import { createRouter, createWebHistory } from "vue-router";

const routes = [
  {
    path: "/",
    name: "home",
    component: () =>
      import(/* webpackChunkName: "home" */ "../views/HelloWorld.vue"),
  },
  {
    path: "/about",
    name: "about",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
  },
  {
    path: "/tokens",
    name: "tokensview",
    component: () =>
      import(/* webpackChunkName: "artistsview" */ "../views/TokenView.vue"),
  },
  {
    path: "/collections",
    name: "collectionsview",
    component: () =>
      import(
        /* webpackChunkName: "collectionsview" */ "../views/collectionsView.vue"
      ),
  },
  {
    path: "/foradminview",
    name: "foradminview",
    component: () =>
      import(
        /* webpackChunkName: "foradminview" */ "../views/forAdminView.vue"
      ),
  },
  {
    path: "/forartistsview/:projectid",
    name: "forartistsview",
    component: () =>
      import(
        /* webpackChunkName: "forartistsview" */ "../views/forArtistsView.vue"
      ),
  },
  {
    path: "/inmintingproject",
    name: "inmintingprojectview",
    component: () =>
      import(
        /* webpackChunkName: "inmintingprojectview" */ "../views/inMintingProjectView.vue"
      ),
  },
  {
    path: "/roadmap",
    name: "roadmapview",
    component: () =>
      import(/* webpackChunkName: "roadmapview" */ "../views/roadmapView.vue"),
  },
  {
    path: "/aboutcollection/:projectid",
    name: "aboutcollection",
    component: () =>
      import(
        /* webpackChunkName: "roadmapview" */ "../views/aboutCollection.vue"
      ),
  },
  {
    path: "/minttoken/:projectid",
    name: "minttoken",
    component: () =>
      import(/* webpackChunkName: "roadmapview" */ "../views/mintToken.vue"),
  },
  {
    path: "/:pathMatch(.*)*",
    name: "PageNotFound",
    component: () =>
      import(
        /* webpackChunkName: "roadmapview" */ "../components/pageNotFound.vue"
      ),
  },
  {
    path: "/artistprojects",
    name: "ArtistProjects",
    component: () =>
      import(
        /* webpackChunkName: "roadmapview" */ "../views/ArtistProjects.vue"
      ),
  },
  {
    path: "/mytokens",
    name: "mytokens",
    component: () =>
      import(/* webpackChunkName: "roadmapview" */ "../views/mytokensView.vue"),
  },
  {
    path: "/jsonmaker/:projectId",
    name: "jsonMaker",
    component: () =>
      import(
        /* webpackChunkName: "roadmapview" */ "../components/jsonMaker.vue"
      ),
  },
]; //mintToken

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
